import toString from '../lang/toString'

/**
 *  Function 
 * ========================================================================
 * @method isFunction
 * @param {*} val - 
 * @returns {boolean} 'val'  Function  true false
 */
const isFunction = (val) => {
  return typeof val === 'function' || toString(val) === '[object Function]'
}

export default isFunction
