/**
 * 
 * ========================================================================
 * @method hasOwn
 * @param {Object} obj - 
 * @param {String} prop - 
 * @returns {Boolean}
 */
const hasOwn = (obj, prop) => {
  const hasOwnProperty = Object.prototype.hasOwnProperty
  return obj && hasOwnProperty.call(obj, prop)
}

export default hasOwn
