import matches from './matches'
import getParentOrHost from './getParentOrHost'

/**
 *  options  selector 
 * ========================================================================
 * @method closest
 * @param {HTMLElement} el - DOM 
 * @param {String} selector - DOM 
 * @param {HTMLElement} [ctx] -  DOM 
 * @param {Boolean} [includeCTX] -  context DOM 
 * @returns {null|HTMLElement} -  DOM 
 */
const closest = (el, selector, ctx, includeCTX) => {
  const context = ctx || document

  if (!el) {
    return null
  }

  do {
    /* istanbul ignore else */
    if (
      (selector != null &&
        (selector.startsWith('>')
          ? el.parentNode === context && matches(el, selector)
          : matches(el, selector))) ||
      (includeCTX && el === context)
    ) {
      return el
    }

    /* istanbul ignore else */
    if (el === context) {
      break
    }

    /* jshint boss:true */
  } while ((el = getParentOrHost(el)))
}

export default closest
