/**
 *  HTML 
 *
 * @param {String} str - 
 * @returns {String}
 */
const encodeHTML = (str) => {
  const CHARTS = {
    '&': '&amp;',
    '<': '&lt;',
    '>': '&gt;',
    "'": '&#39;',
    '"': '&quot;'
  }

  return str.replace(/[&<>'"]/g, (tag) => {
    return CHARTS[tag] || tag
  })
}

export default encodeHTML
