import toString from '../lang/toString'

/**
 *  Array 
 * ========================================================================
 * @method isArray
 * @param {*} o
 * @returns {boolean}
 */
const isArray = (o) => {
  if (Array.isArray) {
    return Array.isArray(o)
  } else {
    return toString(o) === '[object Array]'
  }
}

export default isArray
