/**
 *  id 
 * ========================================================================
 * @method guid
 * @param {String} [prefix] -  id 
 * @return {String}  id 
 */
const guid = (() => {
  let id = 0

  return (prefix = 'guid-') => {
    id += 1

    return `${prefix + id}`
  }
})()

export default guid
