/**
 *  DOM 
 * ========================================================================
 * @method getParentOrHost
 * @param {*|HTMLElement} el -  DOM 
 * @returns {*|HTMLElement}
 */
const getParentOrHost = (el) => {
  return el.host && el !== document && el.host.nodeType
    ? el.host
    : el.parentNode
}

export default getParentOrHost
