import isString from './utils/types/isString'
import hasOwn from './utils/lang/hasOwn'
import isPlainObject from './utils/types/isPlainObject'
import extend from './utils/lang/extend'
import publish from './utils/observer/emit'
import subscribe from './utils/observer/on'
import unsubscribe from './utils/observer/off'

class Base {
  constructor(options) {
    this.attrs = {}

    if (options) {
      this.initialize(options)
    }
  }

  initialize(options) {
    this.attr(options).render().addListeners()
    return this
  }

  attr(prop, value) {
    const attrs = this.attrs

    if (isString(prop)) {
      //  attrs 
      if (value && hasOwn(attrs, prop)) {
        // 
        attrs[prop] = value
        return this
      }

      //  prop 
      return attrs[prop]
    } else if (isPlainObject(prop)) {
      // 
      extend(attrs, prop)

      return this
    } else if (arguments.length === 0) {
      // 
      return attrs
    }

    return this
  }

  render() {
    return this
  }

  destroy() {
    this.removeListeners()
    return this
  }

  reload(options) {
    this.destroy().initialize(this.attr(options))
    return this
  }

  $emit(event, data) {
    publish(event, data)
    return this
  }

  $on(event, callback) {
    subscribe(event, callback, this)
    return this
  }

  $off(event, callback) {
    unsubscribe(event, callback)
    return this
  }

  addListeners() {
    return this
  }

  removeListeners() {
    return this
  }
}

export default Base
