/**
 * Safari 
 * resolveTextNode() 
 * ========================================================================
 * @method resolveTextNode
 * @param {HTMLElement|Text} el - 
 * @return {*|HTMLElement} -  DOM 
 */
const resolveTextNode = function (el) {
  if (el && el.nodeType === 3) {
    return el.parentNode
  }

  return el
}

export default resolveTextNode
