import closest from '../dom/closest'
import off from './off'
import getTarget from './getTarget'

import { CAPTURE_EVENTS } from './enum'

/**
 * 
 * ========================================================================
 * @method on
 * @param {HTMLElement|String|Object} el -  DOM 
 * @param {String} selector -  DOM 
 * @param {String|Function} type - 
 * @param {Function|Object} fn -  
 * @param {Object|Boolean} [data] -  this 
 * @param {Object|Boolean} [context] -  this 
 *  true  this  data 
 * @param {Boolean} once - 
 */
const on = (el, selector, type, fn, data, context, once = false) => {
  // CAPTURE_EVENTS 
  const capture = CAPTURE_EVENTS.indexOf(type) > -1

  const listener = function (evt) {
    const target = getTarget(evt)
    //  Element.matches 
    const delegateTarget = closest(target, selector, el)
    let overrideContext = context || el

    evt.delegateTarget = delegateTarget

    //  true 
    // this  data 
    if (context === true) {
      overrideContext = data
    }

    /* istanbul ignore else */
    if (delegateTarget) {
      // 
      /* istanbul ignore else */
      if (once === true) {
        off(el, type, listener)
      }

      fn.call(overrideContext, evt, data)
    }
  }

  if (!el._listeners) {
    el._listeners = []
  }

  //  options 
  el._listeners.push({
    el,
    selector,
    type,
    fn: listener,
    data,
    context,
    capture
  })

  // 
  fn._delegateListener = listener

  el.addEventListener(type, listener, capture)
}

export default on
