import purgeElement from './purgeElement'
import isFunction from '../types/isFunction'
import _off from './_off'

/**
 *  type 
 * ========================================================================
 *  handler this.$options  type 
 * ========================================================================
 * @method off
 * @param {HTMLElement|Object} el -  DOM 
 * @param {String} type - 
 * @param {Function} [fn] - 
 */
const off = (el, type, fn) => {
  //  fn  el 
  if (!isFunction(fn)) {
    return purgeElement(el, type)
  }

  _off(el, type, fn)
}

export default off
